<template>
    <div>
        <div  v-for="admin in admins" v-bind:key="admin.id">
        <router-link id="choose_admin" tag="div" v-bind:to="{name:'user-registration',params:{admin_id:admin.id}}"
                    >
            <div class="row">
                <div class="col-sm-12">
                    <div class="col-sm-4 admin_list_box">
                        <div class="admin_list_pic">
                            <img src="@/assets/img/01.jpg" width="100%"/>
                        </div>
                        <div class="admin_name">{{ admin.adminName}}</div>
                        <div class="admin_desig">{{ admin.adminType}}</div>
                    </div>
                </div>
            </div>
        </router-link>
        </div>
    </div>
</template>
<script>
import db from '@/components/js/fireBaseInit'

export default {
  name: 'choose_admin',
  data: function () {
    return {
      admins: [],
      date: new Date().toDateString().substr(4)
    }
  },
  created: function () {
    db.collection('ActiveAdmins').doc(this.date).collection('AdminList').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        db.collection('Admins').doc(doc.id).get().then(docum => {
          const data = {
            'id': docum.id,
            'adminName': docum.data().first_name + ' ' + docum.data().last_name,
            'adminType': docum.data().admin_desig,
            'admin_id': docum.data().admin_id
          }
          this.admins.push(data)
        })
      })
    })
  }
}
</script>

<style scoped>

</style>



