<template>
  <div id="editadmin" class="editAdmin">Edit
    <div class="row">
      <form @submit.prevent="updateAdmin" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <span>NAME</span>
            <input type="text" v-model="admin[0].name" required>

          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <span>EMAIL</span>
            <input type="text" v-model="admin[0].email" required>

          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <span>PHONENUMBER</span>
            <input type="text" v-model="admin[0].phone" required>

          </div>
        </div>
        <button type="submit" class="btn">submit</button>
        <router-link to="/" class="btn grey">cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from '../js/fireBaseInit'
export default {
  name: 'editAdmin',
  data () {
    return {
      name: null,
      email: null,
      phone: null,
      admin: []

    }
  },
  created () {
    db.collection('Admins').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        if (parseInt(doc.id) === parseInt(this.$route.params.admin_id)) {
          console.log(doc.data())
          const data = {
            name: doc.data().first_name,
            email: doc.data().email_id,
            phone: doc.data().phone_number
          }
          this.admin.push(data)
          console.log(this.admin[0])
        }
      })
    })
  },
  methods: {
    // deleteEmployee () {
    //   if (confirm('are you sure')) {
    //     db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
    //       querySnapshot.forEach(doc => {
    //         doc.ref.delete()
    //         this.$route.push('/')
    //       })
    //     })
    //   }
    // },
    updateAdmin () {
      console.log('fuctin')
      db.collection('Admins').get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          if (parseInt(doc.id) === parseInt(this.$route.params.admin_id)) {
            console.log(doc.data())
            doc.ref.update({
              first_name: this.admin[0].name,
              email_id: this.admin[0].email,
              phone_number: this.admin[0].phone
            })
              .then(() => {
                console.log(this.$route.params.admin_id)
                this.$router.push({name: 'adminSchedule', params: {admin_id: this.$route.params.admin_id}})
              })
          }
        })
      })
    }
  }
}
</script>

<style scoped>
  /*@import 'https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0/css/materialize.min.css';*/
  .input-field {
border: none;
    border-bottom: 1px solid #5e5e5e;
    border-radius: 0;
    outline: none;
    height: 3rem;
    width: 100%;
    font-size: 16px;
    margin: 0 0 80px 0;
    box-sizing: content-box;

  }
div.editAdmin {
  padding-left: 33px;
}
</style>



