<template>
  <div class="col-sm-12 center login_box text_center">
    <form class="text_left" @submit.prevent="tokenfrm">
      <div class="form-group col-sm-4 col-sm-offset-4">
        <i class="fa fa-key form_icon" aria-hidden="true"></i>
        <input type="password" class="form-control text_box" id="roomNumber" placeholder="Enter token number">
      </div>
      <div class="row">
        <div class="form-group col-sm-4 col-sm-offset-4">
          <button type="submit" class="btn btn-default btn-block main_button">Re-join Queue</button>
        </div>
      </div>
      <br><br>
    </form>
  </div>
</template>

<script>
export default {
  name: 'Verify',
  methods: {
    tokenfrm: function () {
      var code = document.getElementById('roomNumber');
      window.confirmationResult.confirm(code).then(function (result) {
        // User signed in successfully.
        var user = result.user;
        this.$router.push('/register_form.vue')
        // ...
      })
    }
  }
}
</script>

<style scoped>

</style>



