import Vue from 'vue'
import Router from 'vue-router'
import userRegistration from '@/components/pages/user/user_registration'
import userLogin from '@/components/pages/user/user_login'
import clientDashboard from '@/components/pages/user/client_dashboard'
import rejoin from '@/components/pages/user/re-join'
import adminSchedule from '../components/pages/admin/adminSchedule'
import adminLogin from '../components/pages/admin/adminLogin'
import adminMeeting from '../components/pages/admin/adminMeeting'
import editAdmin from '../components/widgets/editAdmin'
import Verify from '../components/pages/user/Verify'
import VueTelInput from 'vue-tel-input'

Vue.use(VueTelInput)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: userLogin
    },
    {
      path: '/register/:admin_id',
      name: 'user-registration',
      component: userRegistration
    },
    {
      path: '/editAdmin/:admin_id',
      name: 'editAdmin',
      component: editAdmin
    },
    {
      path: '/dashboard/:admin_id /:user_id',
      name: 'dashboard',
      component: clientDashboard
    },
    {
      path: '/rejoin/:admin_id',
      name: 'rejoin',
      component: rejoin
    },
    {
      path: '/admin',
      component: adminLogin

    },
    {
      path: '/meeting/:admin_id',
      name: 'adminMeeting',
      component: adminMeeting

    },
    {
      path: '/schedule/:admin_id',
      name: 'adminSchedule',
      component: adminSchedule

    },
    {
      path: '/verify',
      name: 'verify',
      component: Verify

    }
  ]
})



