<template>
    <div class="col-sm-12 center login_box text_center">
        <form class="text_left" v-on:submit.prevent="verify">
            <div class="form-group col-sm-4 col-sm-offset-4">
                <i class="fa fa-user form_icon" aria-hidden="true"></i>
                <vue-tel-input type="text" class="form-control text_box" id="phnnumber" placeholder="Enter phone number"
                               v-model="phoneNumber" :preferredCountries="['us', 'gb', 'ua']" ></vue-tel-input>
            </div>
            <div class="row">
                <div class="form-group col-sm-4 col-sm-offset-4">
                    <button type="submit" class="btn btn-default btn-block main_button" id="verifybtn"
                            @click="verifyfrm">Verify
                    </button>
                </div>
            </div>
            <!--<div id="recaptcha-container"></div>-->
            <div class="form-group col-sm-4 col-sm-offset-4">
                <i class="fa fa-key form_icon" aria-hidden="true"></i>
                <input type="password" class="form-control text_box" id="roomNumber" placeholder="Enter token number"
                       v-model="tokenNumber">
            </div>
            <div class="row">
                <div class="form-group col-sm-4 col-sm-offset-4">
                    <button type="button" class="btn btn-default btn-block main_button" id="tokenbtn" @click="tokenfrm">
                        Re-join Queue
                    </button>
                </div>
                </br></br>
                <br><br>
            </div>
        </form>
    </div>

</template>

<script>
import firebase from 'firebase'
import db from '@/components/js/fireBaseInit'
import router from '@/router'
export default {
  name: 'rejoin_form',
  data () {
    return {
      phoneNumber: '',
      tokenNumber: '',
      date: new Date().toDateString().substr(4),
      phone_number: '',
      id: ''
    }
  },
  methods: {
    verifyfrm: function () {
      db.collection('PhoneNumbers').doc(this.date).collection('PhoneNumber').doc(this.phoneNumber).get()
        .then(querySnapshot => {
          if (querySnapshot.exists) {
            this.authenticate()
          } else {
            alert('Phone number not yet registered')
          }
        })
    },
    authenticate: function () {
      window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('verifybtn', {
        'size': 'invisible'
      })
      firebase.auth().signInWithPhoneNumber(this.phoneNumber, window.recaptchaVerifier)
        .then(function (confirmationResult) {
          // SMS sent. Prompt user to type the code from the message, then sign the
          // user in with confirmationResult.confirm(code).
          window.confirmationResult = confirmationResult
          console.log(window.confirmationResult)
        })
    },
    tokenfrm: function () {
      console.log(window.confirmationResult)
      window.confirmationResult.confirm(this.tokenNumber).then(function (result) {
        // User signed in successfully.
        console.log('result', result)
        router.push({name: 'dashboard', params: {admin_id: router.currentRoute.params.admin_id, user_id: router.currentRoute.params.user_id}})
      })
    },
    onInput ({ number, isValid, country }) {
      console.log(number, isValid, country)
    }

  }
}
</script>

<style scoped>

</style>



