<template>
    <div id="register_form">
        <div class="col-sm-8 side_left flex full_height">
            <div class="col-md-10 col-sm-8 center login_box text_center">
                <image_logo_b></image_logo_b>
                <h1 class="main_heading">Please fill the below Registration form</h1>
                <p class="sub_head">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
                </br>
                <div class="col-sm-12 center login_box text_center">
                    <form class="text_left" @submit.prevent="checkPhoneNumber">
                        <div class="row">
                            <div class="form-group col-sm-6">
                                <i class="fa fa-user form_icon" aria-hidden="true"></i>
                                <input type="text" class="form-control text_box" v-model="first_name" id="first_name"
                                       placeholder="First Name" required>
                            </div>
                            <div class="form-group col-sm-6">
                                <i class="fa fa-user form_icon" aria-hidden="true"></i>
                                <input type="text" class="form-control text_box" id="last_name" v-model="last_name"
                                       placeholder="Last Name" required>
                            </div>
                        </div>
                        <div class="row">
                            <div class="form-group col-sm-6">
                                <i class="fa fa-envelope form_icon" aria-hidden="true"></i>
                                <input type="text" class="form-control text_box" id="email" v-model="email_id"
                                       placeholder="Email" required>
                            </div>
                            <div class="form-group col-sm-6">
                                <i class="fa fa-phone form_icon" aria-hidden="true"></i>
                                <vue-tel-input  class="form-control text_box" id="phn" v-model="phone_number" @onInput="onInput"
                                               placeholder="Phone Number" required :preferredCountries="['us', 'gb', 'ua']"></vue-tel-input>
                            </div>
                        </div>
                        <div class="row">
                            <div class="form-group col-sm-12">
                                <div class="input-group">
                                    <div class="input-group-btn col-sm-3">
                                        <!--<button type="button" class="btn btn-secondary dropdown-toggle text_box drop_btn" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">-->
                                        <!--<i class="fa fa-sort-amount-asc" aria-hidden="true"></i> Select ID-->
                                        <!--</button>-->
                                        <div>
                                            <select class="btn btn-secondary dropdown-toggle text_box drop_btn"
                                                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"
                                                    v-model="id" style="padding-bottom: 18px;padding-top: 18px;">
                                                <option class="dropdown-item" disabled><i class="fa fa-sort-amount-asc"
                                                                                          aria-hidden="true"></i> Select
                                                    ID
                                                </option>
                                                <option class="dropdown-item" value="adhaar">Aadhaar ID</option>
                                                <option class="dropdown-item" value="election">Election ID</option>
                                                <option class="dropdown-item" value="licence">License</option>
                                                <option class="dropdown-item" value="pancard">Pancard</option>
                                            </select>

                                        </div>
                                    </div>
                                    <input type="text" class="form-control text_box" id="id_number" v-model="id_number"
                                           placeholder="ID Number" required>
                                </div>
                            </div>
                        </div>
                        <!--<vue-tel-input v-model="phone"-->
                                       <!--@onInput="onInput"-->
                        <!--:preferredCountries="['us', 'gb', 'ua']">-->
                        <!--</vue-tel-input>-->
                        <div class="row">
                            <div class="form-group col-sm-12">
                                <i class="fa fa-desktop form_icon" aria-hidden="true"></i>
                                <input type="text" class="form-control text_box" id="meeting_type" v-model="purpose"
                                       placeholder="Type your purpose of meeting" required>
                            </div>
                        </div>
                        <div class="row">
                            <div class="form-group col-sm-6 col-lg-5">
                                <router-link tag="button" type="submit" class="btn btn-default btn-block cancel_button"
                                             v-bind:to="{name:'rejoin' , params:{admin_id:this.$route.params.admin_id}}">
                                    I'm already registered!
                                </router-link>
                            </div>
                            <div class="form-group col-sm-6 col-lg-5 col-lg-offset-2">
                                <button type="submit" class="btn btn-default btn-block main_button">Register</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import image_logo_b from '@/components/shared/image_logo_b'
import db from '@/components/js/fireBaseInit'

export default {
  name: 'register_form',
  components: {
    image_logo_b
  },
  data () {
    return {
      first_name: null,
      last_name: null,
      email_id: null,
      purpose: null,
      id_number: null,
      phone_number: null,
      id: null,
      user_id: 'u' + Math.floor(Math.random() * 10000000000),
      date: new Date().toDateString().substr(4),
      phone: ''
    }
  },
  methods: {
    checkPhoneNumber () {
      db.collection('PhoneNumbers').doc(this.date).collection('PhoneNumber').doc(this.phone_number).get().then(querySnapshot => {
        if (querySnapshot.exists) {
          alert('This phone number has been already registered!!!!!   Try Again')
        } else {
          this.saveActiveUser()
        }
      })
    },
    saveActiveUser () {
      db.collection('ActiveUsers').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id).collection('UserList').doc(this.user_id).set({
        first_name: this.first_name,
        last_name: this.last_name,
        purpose: this.purpose,
        email_id: this.email_id,
        phone_number: this.phone_number,
        Identification: this.id_number,
        user_id: this.user_id,
        time: new Date().getTime()
      }).then(doc => {
        this.saveNumber()
      }).catch(err => {
        console.log(err)
      })
    },
    saveNumber () {
      db.collection('PhoneNumbers').doc(this.date).collection('PhoneNumber').doc(this.phone_number).set({
        user_id: this.user_id,
        admin_id: this.$route.params.admin_id
      }).then(docref => {
        console.log('User Registered')
        this.$router.push({
          name: 'dashboard',
          params: {admin_id: this.$route.params.admin_id, user_id: this.user_id}
        })
      }).catch(err => {
        console.log(err)
      })
    },
    onInput ({ number, isValid, country }) {
      console.log(number, isValid, country)
    }

  }
}
</script>

<style scoped>
    /*@import 'vue-tel-input/dist/vue-tel-input.css';*/
</style>



