</template>

<script>
    import cancel_form from '@/components/shared/cancel_form'
    import image_logo_b from '@/components/shared/image_logo_b'
  export default {
    name: 'client_dashboard_left',
    components:{
      cancel_form,
      image_logo_b
    }
  }
</script>

