<template>
    <div class="col-sm-12 no_padding user_card_height">
        <!-- ------------- User Card -------------- -->
        <div class="user_card text_left">
            <div class="user_pic flex">
                <div class="pic"><i class="fa fa-user" aria-hidden="true"></i></div>
            </div>
            <div class="user_box">
                <div class="username">
                    <span>Username</span>
                    <i class="fa fa-caret-down" aria-hidden="true"></i>
                </div>
            </div>
            <!-- ------------- Dropdown Box -------------- -->
            <div class="dropdown">
                <ul>
                    <li data-toggle="modal" data-target="#profile_view"><i class="fa fa-user" aria-hidden="true"></i>  My Profile</li>
                    <li data-toggle="modal" data-target="#settings_view"><i class="fa fa-cog" aria-hidden="true"></i>  Settings</li>
                    <li><i class="fa fa-sign-out-alt" aria-hidden="true"></i>  Exit</li>
                </ul>
            </div>
            <div class="modal fade " id="profile_view" tabindex="-1" role="dialog">
                <div class="modal-dialog modal-lg profile_view" role="document">
                    <div class="modal-content">
                        <div class="modal-body">
                            <div class="row">
                                <h1 class="main_heading">My Profile</h1><br>
                                <div class="col-sm-4 text_center">
                                    <div class="profile_pic">
                                        <i class="fa fa-user" aria-hidden="true"></i>
                                    </div>
                                </div>
                                <div class="col-sm-8">
                                    <table class="table profile_table">
                                        <tbody>
                                        <tr>
                                            <td width="150px">Name</td>
                                            <td>:</td>
                                            <td>{{ user[0].first_name}} {{ user[0].last_name}}</td>
                                        </tr>
                                        <tr>
                                          <td>Email</td>
                                            <td>:</td>
                                            <td> {{ user[0].email_id}}</td>
                                        </tr>
                                        <tr>
                                            <td>Phone</td>
                                            <td>:</td>
                                            <td> {{ user[0].phone_number}}</td>
                                        </tr>
                                        <tr>
                                            <td>ID Proof</td>
                                            <td>:</td>
                                            <td>ADR12015356656</td>
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <button type="button" data-toggle="modal" data-target="#edit_view" class="btn btn-default main_button edit_button pull-left">Edit Profile</button>
                            <button type="button" class="btn btn-default main_button" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                    <!-- /.modal-content -->
                </div>
                <!-- /.modal-dialog -->
            </div>
          <div class="modal fade " id="edit_view" tabindex="-1" role="dialog">
            <div class="modal-dialog modal-lg profile_view" role="document">
              <div class="modal-content">
                <div class="modal-body">
                  <div class="row">
                    <h1 class="main_heading">My Profile</h1><br>
                    <div class="col-sm-4 text_center">
                      <div class="profile_pic">
                        <i class="fa fa-user" aria-hidden="true"></i>
                      </div>
                    </div>
                    <div class="col-sm-8">
                      <div id="edituser" class="editUser">
                        <div class="row">
                          <form @submit.prevent="updateUser" class="col s12">
                            <div class="row">
                              <div class="input-field col s12">
                                <span>NAME</span>
                                <input type="text" v-model="user[0].first_name" required>

                              </div>
                            </div>
                            <div class="row">
                              <div class="input-field col s12">
                                <span>EMAIL</span>
                                <input type="text" v-model="user[0].email_id" required>

                              </div>
                            </div>

                            <div class="row">
                              <div class="input-field col s12">
                                <span>PHONENUMBER</span>
                                <input type="text" v-model="user[0].phone_number" required>

                              </div>
                            </div>
                            <button type="submit" class="btn">submit</button>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="modal-footer">
                  <button type="button" class="btn btn-default main_button" data-dismiss="modal">Close</button>
                </div>
              </div>
              <!-- /.modal-content -->
            </div>
            <!-- /.modal-dialog -->
          </div>
        </div>
    </div>
</template>

<script>
import db from '../js/fireBaseInit'
export default {
  name: 'user_account',
  data () {
    return {
      user: [],
      user_id: this.$route.params.user_id,
      date: new Date().toDateString().substr(4)
    }
  },
  mounted () {
    console.log('hh')
    console.log(this.date)
    console.log(this.$route.params.admin_id)
    console.log(this.$route.params.user_id)
    db.collection('ActiveUsers').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id).collection('UserList').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log('this', doc.id)
        if ((doc.id) === (this.$route.params.user_id)) {
          console.log(doc.data())
          const data = {
            first_name: doc.data().first_name,
            last_name: doc.data().last_name,
            email_id: doc.data().email_id,
            phone_number: doc.data().phone_number
          }
          this.user.push(data)
          console.log(this.user[0].first_name)
        }
      })
    })
  },
  updateUser () {
    console.log('fuctin')
    db.collection('ActiveUsers').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id).collection('UserList').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log('this', doc.id)
        if ((doc.id) === (this.$route.params.user_id)) {
          doc.ref.update({
            first_name: this.user[0].first_name,
            email_id: this.user[0].email_id,
            phone_number: this.user[0].phone_number
          })
        }
      })
    })
  }
}
</script>

<style scoped>
  .input-field {
    border: none;
    border-bottom: 1px solid #5e5e5e;
    border-radius: 0;
    outline: none;
    height: 3rem;
    width: 100%;
    font-size: 16px;
    margin: 0 0 80px 0;
    box-sizing: content-box;

  }
</style>



