<template>
  <div class="container-fluid login_bg full_height">
    <div class="row full_height">
      <div class="col-sm-8 side_left flex full_height">
        <login-form></login-form>
      </div>
      <div class="col-sm-4 side_right text_center flex full_height">
        <log-info></log-info>
        <div class="row footer">
          <page-footer></page-footer></div>
      </div>
    </div>
  </div>
</template>

<script>
import logInfo from '@/components/widgets/info/logInfo'
import loginForm from '@/components/widgets/loginForm'
import pageFooter from '@/components/shared/pageFooter'
export default {
  name: 'adminLogin',
  components: {
    pageFooter,
    loginForm,
    logInfo
  }
}
</script>

<style scoped>

</style>



